* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --black: #131314;
  --white: #fff;
  --cream: #faf9f0;
  --gray: #8b8b8b;
  --light-gray: #e5e5e5;
  --blue: #2b5ce6;
  --orange: #d97757;
  --grid-size: 40px;
}

body {
  background: var(--cream);
  color: var(--black);
  font-family: Lora, serif;
  line-height: 1.6;
}

h1, h2, h3, h4, h5, h6 {
  font-family: Inter, sans-serif;
  font-weight: 600;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(.9);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.animate-fade-in-up {
  animation: .8s ease-out forwards fadeInUp;
}

.animate-fade-in {
  animation: 1s ease-out forwards fadeIn;
}

.animate-slide-right {
  animation: .8s ease-out forwards slideInRight;
}

.animate-slide-left {
  animation: .8s ease-out forwards slideInLeft;
}

.animate-scale {
  animation: .6s ease-out forwards scaleIn;
}

.delay-1 {
  opacity: 0;
  animation-delay: .1s;
}

.delay-2 {
  opacity: 0;
  animation-delay: .2s;
}

.delay-3 {
  opacity: 0;
  animation-delay: .3s;
}

.delay-4 {
  opacity: 0;
  animation-delay: .4s;
}

.delay-5 {
  opacity: 0;
  animation-delay: .5s;
}

.top-bar {
  background: var(--black);
  color: var(--white);
  letter-spacing: .5px;
  justify-content: space-between;
  padding: 10px 40px;
  font-family: Inter, sans-serif;
  font-size: 11px;
  display: flex;
}

.top-bar-links a {
  color: var(--white);
  text-transform: uppercase;
  margin-left: 20px;
  text-decoration: none;
  transition: color .3s;
}

.top-bar-links a:hover {
  color: var(--orange);
}

nav {
  background: var(--cream);
  z-index: 100;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  display: flex;
  position: relative;
}

.nav-left {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.logo {
  letter-spacing: 1px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  transition: all .3s;
}

.nav-subtitle {
  color: var(--gray);
  font-family: Inter, sans-serif;
  font-size: 12px;
}

.hamburger {
  cursor: pointer;
  background: none;
  border: none;
  flex-direction: column;
  gap: 5px;
  padding: 10px;
  display: none;
}

.hamburger span {
  background: var(--black);
  width: 25px;
  height: 2px;
  transition: all .3s;
}

.hamburger.active span:first-child {
  transform: rotate(45deg)translate(5px, 5px);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
}

.hamburger.active span:nth-child(3) {
  transform: rotate(-45deg)translate(6px, -6px);
}

.nav-menu {
  gap: 40px;
  margin-left: auto;
  list-style: none;
  display: flex;
}

.nav-menu a {
  color: var(--black);
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  position: relative;
}

.nav-menu a:after {
  content: "";
  background: var(--blue);
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.nav-menu a:hover:after {
  width: 100%;
}

.menu-overlay {
  display: none;
}

@media (width <= 768px) {
  .menu-overlay, .menu-overlay.active {
    display: none;
  }
}

.main-grid {
  background-image: linear-gradient(var(--light-gray) 1px, transparent 1px), linear-gradient(90deg, var(--light-gray) 1px, transparent 1px);
  background-size: var(--grid-size) var(--grid-size);
  grid-template-columns: repeat(12, 1fr);
  min-height: calc(100vh - 150px);
  display: grid;
}

.content-left {
  background: var(--white);
  border-right: 2px solid var(--black);
  flex-direction: column;
  grid-column: 1 / 8;
  justify-content: center;
  padding: 60px 40px;
  display: flex;
}

.hero-label {
  letter-spacing: 1px;
  text-transform: uppercase;
  width: fit-content;
  margin-bottom: 40px;
  font-family: Inter, sans-serif;
  font-size: 15px;
  font-weight: 600;
  display: inline-block;
}

h1 {
  text-transform: uppercase;
  letter-spacing: -1px;
  margin-bottom: 40px;
  font-size: 4.5vw;
  font-weight: 700;
  line-height: .95;
}

.hero-description {
  max-width: 500px;
  color: var(--black);
  margin-bottom: 50px;
  font-size: 18px;
  line-height: 1.6;
}

.cta-button {
  background: var(--black);
  color: var(--white);
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  width: fit-content;
  padding: 18px 40px;
  font-family: Inter, sans-serif;
  font-size: 13px;
  font-weight: 600;
  transition: all .3s;
}

.sidebar-right {
  background: var(--cream);
  object-fit: scale-down;
  flex-direction: column;
  grid-column: 8 / 13;
  align-items: center;
  padding: 60px 40px;
  display: flex;
  position: relative;
}

.profile-photo-container {
  border: 1px solid var(--black);
  background: var(--cream);
  border-radius: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 450px;
  height: 450px;
  margin-bottom: 40px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.profile-photo {
  margin-top: 50%;
}

.sidebar-title {
  letter-spacing: 2px;
  text-transform: uppercase;
  width: 100%;
  margin-bottom: 30px;
  padding-bottom: 10px;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 600;
}

.info-blocks {
  flex-direction: column;
  gap: 20px;
  width: 100%;
  display: flex;
}

.info-block {
  border: 2px solid var(--black);
  background: var(--white);
  cursor: pointer;
  padding: 20px;
  transition: all .3s;
}

.info-block:hover {
  box-shadow: 5px 5px 0 var(--black);
  transform: translateX(-5px);
}

.info-block h3 {
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 10px;
  font-family: Inter, sans-serif;
  font-size: 13px;
  font-weight: 700;
}

.info-block p {
  color: var(--gray);
  font-size: 12px;
  line-height: 1.6;
}

.info-block .meta {
  text-transform: uppercase;
  letter-spacing: .5px;
  margin-top: 10px;
  font-family: Inter, sans-serif;
  font-size: 10px;
}

.featured-section {
  background: var(--cream);
  padding: 80px 40px;
}

.section-header {
  margin-bottom: 60px;
}

.section-label {
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 20px;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 600;
}

.section-title {
  letter-spacing: -1px;
  font-size: 56px;
  font-weight: 700;
}

.projects-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  display: grid;
}

.project-card {
  background: var(--white);
  border: 1px solid var(--black);
  cursor: pointer;
  border-radius: 12px;
  flex-direction: column;
  transition: all .3s;
  display: flex;
  overflow: hidden;
}

.project-card:hover {
  box-shadow: 10px 10px 0 var(--black);
  transform: translateY(-10px);
}

.project-image {
  background: var(--white);
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.project-image img {
  width: 100%;
  height: auto;
  display: block;
}

.project-card-overlay {
  background: var(--white);
  color: var(--black);
  border-top: 1px solid var(--black);
  padding: 30px;
  transition: all .3s;
}

.project-card-overlay .project-number {
  color: var(--orange);
  margin-bottom: 10px;
  font-family: Inter, sans-serif;
  font-size: 40px;
  font-weight: 700;
  line-height: 1;
}

.project-card-overlay .project-title {
  letter-spacing: -.5px;
  color: var(--black);
  margin-bottom: 8px;
  font-size: 24px;
  font-weight: 700;
}

.project-card-overlay .project-period {
  color: var(--gray);
  margin-bottom: 15px;
  font-family: Inter, sans-serif;
  font-size: 12px;
}

.project-card-overlay .project-tags {
  flex-wrap: wrap;
  gap: 8px;
  display: flex;
}

.project-card-overlay .tag {
  border: 1px solid var(--black);
  color: var(--black);
  text-transform: uppercase;
  letter-spacing: .5px;
  background: none;
  border-radius: 0;
  padding: 4px 10px;
  font-family: Inter, sans-serif;
  font-size: 11px;
}

.project-image-icon {
  color: var(--gray);
  margin-bottom: 10px;
  font-size: 64px;
}

.project-image-text {
  color: var(--gray);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: Inter, sans-serif;
  font-size: 11px;
}

.project-content {
  flex-direction: column;
  flex-grow: 1;
  padding: 30px;
  display: flex;
}

.project-number {
  color: var(--orange);
  margin-bottom: 15px;
  font-family: Inter, sans-serif;
  font-size: 60px;
  font-weight: 700;
  line-height: 1;
}

.project-title {
  letter-spacing: -.5px;
  margin-bottom: 15px;
  font-size: 24px;
  font-weight: 700;
}

.project-description {
  color: var(--gray);
  flex-grow: 1;
  margin-bottom: 25px;
  font-size: 14px;
  line-height: 1.6;
}

.project-tags {
  flex-wrap: wrap;
  gap: 8px;
  display: flex;
}

.tag {
  border: 1px solid var(--black);
  text-transform: uppercase;
  letter-spacing: .5px;
  padding: 6px 12px;
  font-family: Inter, sans-serif;
  font-size: 10px;
  font-weight: 500;
}

.modal-overlay {
  z-index: 1000;
  background: #000000d9;
  justify-content: center;
  align-items: center;
  padding: 40px;
  animation: .3s ease-out fadeIn;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-content {
  background: var(--white);
  border: 2px solid var(--black);
  border-radius: 12px;
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  animation: .3s ease-out slideUp;
  position: relative;
  overflow-y: auto;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-close {
  background: var(--black);
  color: var(--white);
  cursor: pointer;
  z-index: 10;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;
  font-size: 28px;
  line-height: 1;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.modal-close:hover {
  background: var(--orange);
  transform: rotate(90deg);
}

.modal-header {
  padding: 40px 40px 20px;
}

.modal-header .project-number {
  color: var(--orange);
  margin-bottom: 15px;
  font-family: Inter, sans-serif;
  font-size: 60px;
  font-weight: 700;
  line-height: 1;
}

.modal-header .project-title {
  letter-spacing: -.5px;
  margin-bottom: 10px;
  font-size: 32px;
  font-weight: 700;
}

.modal-header .project-subtitle {
  color: var(--gray);
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 500;
}

.modal-header .project-period {
  color: var(--gray);
  font-family: Inter, sans-serif;
  font-size: 14px;
}

.modal-image {
  background: var(--white);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 400px;
  margin-bottom: 30px;
  display: flex;
  overflow: hidden;
}

.modal-image img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.modal-body {
  padding: 0 40px 40px;
}

.modal-body .project-description {
  color: var(--gray);
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 1.7;
}

.modal-body .project-role {
  margin-bottom: 25px;
  font-size: 14px;
}

.modal-body .project-role strong {
  color: var(--black);
  font-weight: 700;
}

.modal-body .project-contributions {
  margin-bottom: 25px;
}

.modal-body .project-contributions strong {
  color: var(--black);
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 700;
  display: block;
}

.modal-body .project-contributions ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.modal-body .project-contributions li {
  color: var(--gray);
  margin-bottom: 8px;
  padding-left: 20px;
  font-size: 14px;
  line-height: 1.6;
  position: relative;
}

.modal-body .project-contributions li:before {
  content: "→";
  color: var(--orange);
  font-weight: 700;
  position: absolute;
  left: 0;
}

.modal-body .project-tags {
  margin-bottom: 30px;
}

.modal-body .project-tags strong {
  color: var(--black);
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 700;
  display: block;
}

.modal-body .tags-container {
  flex-wrap: wrap;
  gap: 8px;
  display: flex;
}

.modal-body .project-links {
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

.project-link {
  color: var(--black);
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 500;
  text-decoration: underline;
  transition: color .3s;
  display: inline-block;
}

.expertise-section {
  background: var(--cream);
  padding: 80px 40px;
}

.expertise-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 60px;
  display: grid;
}

.expertise-card {
  border: 1px solid var(--black);
  border-radius: 12px;
  padding: 50px 40px;
}

.expertise-title {
  letter-spacing: -.5px;
  color: var(--black);
  margin-bottom: 20px;
  font-size: 22px;
  font-weight: 700;
}

.expertise-list {
  color: var(--black);
  font-family: Lora, serif;
  font-size: 14px;
  line-height: 2;
}

.stats-section {
  background: var(--black);
  grid-template-columns: repeat(4, 1fr);
  gap: 0;
  display: grid;
}

.stat-card {
  text-align: center;
  background: var(--white);
  border-right: 2px solid var(--black);
  cursor: pointer;
  padding: 60px 40px;
  transition: all .3s;
}

.stat-card:last-child {
  border-right: none;
}

.stat-card:hover {
  background: var(--cream);
}

.stat-card:hover .stat-number {
  transform: scale(1.1);
}

.stat-number {
  margin-bottom: 12px;
  font-family: Inter, sans-serif;
  font-size: 72px;
  font-weight: 700;
  line-height: 1;
  transition: transform .3s;
}

.stat-label {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 12px;
  font-weight: 600;
}

.contact-section {
  background: var(--black);
  color: var(--white);
  text-align: center;
  padding: 100px 40px;
}

.contact-section h2 {
  letter-spacing: -2px;
  margin-bottom: 30px;
  font-size: 72px;
  font-weight: 700;
}

.contact-section p {
  max-width: 600px;
  margin-bottom: 50px;
  margin-left: auto;
  margin-right: auto;
  font-size: 20px;
}

.contact-buttons {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.contact-button {
  background: var(--white);
  color: var(--black);
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  padding: 20px 50px;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.contact-button:hover {
  background: var(--orange);
  color: var(--white);
  transform: scale(1.05);
}

.contact-button.secondary {
  color: var(--white);
  border: 1px solid var(--white);
  background: none;
  border-radius: 8px;
}

.contact-button.secondary:hover {
  background: var(--orange);
  color: var(--white);
  border-color: var(--orange);
}

footer {
  background: var(--black);
  color: var(--white);
  text-align: center;
  padding: 30px 40px;
  font-family: Inter, sans-serif;
  font-size: 12px;
}

@media (width <= 1200px) {
  .main-grid {
    grid-template-columns: 1fr;
  }

  .content-left {
    border-right: none;
    grid-column: 1;
  }

  .sidebar-right {
    grid-column: 1;
  }

  .projects-grid {
    grid-template-columns: 1fr;
  }

  .stats-section {
    grid-template-columns: repeat(2, 1fr);
  }

  h1 {
    font-size: 56px;
  }
}

@media (width <= 768px) {
  .top-bar {
    text-align: center;
    flex-direction: column;
    gap: 10px;
  }

  nav {
    padding: 15px 20px;
  }

  .hamburger {
    z-index: 10002;
    display: flex;
    position: relative;
  }

  .nav-menu {
    background: var(--cream);
    z-index: 10001;
    border: none;
    flex-direction: column;
    gap: 0;
    width: 70%;
    max-width: 300px;
    height: 100vh;
    margin: 0;
    padding: 80px 30px 30px;
    transition: right .3s;
    position: fixed;
    top: 0;
    right: -100%;
    box-shadow: -3px 0 10px #0000001a;
  }

  .nav-menu.active {
    right: 0;
  }

  .nav-menu li {
    border-bottom: 2px solid var(--light-gray);
    padding: 20px 0;
    list-style: none;
  }

  .nav-menu li:first-child {
    padding-top: 0;
  }

  .nav-menu a {
    color: var(--black);
    width: 100%;
    font-size: 18px;
    font-weight: 600;
    display: block;
  }

  .nav-menu a:after {
    display: none;
  }

  .stats-section, .expertise-grid {
    grid-template-columns: 1fr;
  }

  h1 {
    font-size: 42px;
  }

  .section-title {
    font-size: 38px;
  }

  .contact-section h2 {
    font-size: 48px;
  }

  .profile-photo-container {
    width: 100%;
    max-width: 280px;
  }
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hero-section {
  background: var(--cream);
  text-align: center;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  padding: 100px 40px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero-content {
  width: 100%;
  max-width: 1200px;
}

.hero-title {
  letter-spacing: -2px;
  margin-bottom: 30px;
  font-size: 72px;
  font-weight: 700;
  line-height: 1.1;
}

.hero-subtitle {
  color: var(--orange);
}

.hero-description {
  max-width: 750px;
  color: var(--gray);
  margin: 0 auto 50px;
  font-size: 1.25rem;
  line-height: 1.7;
}

.hero-cta {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.cta-button {
  background: var(--black);
  color: var(--white);
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  width: fit-content;
  padding: 18px 40px;
  font-family: Inter, sans-serif;
  font-size: 13px;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.cta-button:hover {
  background: var(--orange);
  transform: scale(1.05);
}

.cta-button.primary {
  background: var(--black);
}

.cta-button.secondary {
  border: 2px solid var(--black);
  color: var(--black);
  background: none;
}

.cta-button.primary:hover {
  background: var(--orange);
  transform: scale(1.05);
}

.cta-button.secondary:hover {
  background: var(--orange);
  color: var(--white);
  border-color: var(--orange);
  transform: scale(1.05);
}

.about-section {
  background: var(--cream);
  padding: 80px 40px;
}

.about-content {
  max-width: 900px;
  margin: 0 auto;
}

.about-content-wrapper {
  grid-template-columns: 1.5fr 1fr;
  align-items: center;
  gap: 60px;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
}

.about-text p {
  color: var(--black);
  margin-bottom: 25px;
  font-size: 18px;
  line-height: 1.8;
}

.about-text strong {
  color: var(--black);
  font-weight: 600;
}

.about-experience-header {
  letter-spacing: -.5px;
  margin-top: 40px;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 700;
}

.about-experience-text {
  font-size: 16px !important;
}

.about-highlights {
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 40px;
  padding-top: 30px;
  display: grid;
}

.highlight-item {
  text-align: center;
}

.highlight-number {
  color: var(--orange);
  margin-bottom: 10px;
  font-family: Inter, sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 1;
}

.highlight-label {
  text-transform: uppercase;
  letter-spacing: 1px;
  color: var(--gray);
  font-size: 12px;
  font-weight: 600;
}

.about-photo {
  position: relative;
}

.profile-photo-frame {
  aspect-ratio: 3 / 4;
  border: 1px solid var(--black);
  background: var(--white);
  border-radius: 16px;
  width: 100%;
  max-width: 400px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 30px #0000001a;
}

.profile-photo-frame img {
  object-fit: cover;
  object-position: center top;
  width: 100%;
  height: 100%;
}

.project-subtitle {
  color: var(--gray);
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 500;
}

.project-period {
  color: var(--gray);
  text-transform: uppercase;
  letter-spacing: .5px;
  margin-bottom: 15px;
  font-family: Inter, sans-serif;
  font-size: 12px;
}

.project-role {
  margin-bottom: 15px;
  padding: 10px 0;
  font-size: 13px;
}

.project-contributions {
  margin-bottom: 20px;
  font-size: 13px;
}

.project-contributions strong {
  margin-bottom: 10px;
  font-weight: 600;
  display: block;
}

.project-contributions ul {
  color: var(--gray);
  margin-left: 20px;
}

.project-contributions li {
  margin-bottom: 8px;
  line-height: 1.5;
}

.project-links {
  flex-wrap: wrap;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.project-link {
  color: var(--black);
  text-transform: uppercase;
  letter-spacing: .5px;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 600;
  text-decoration: none;
  transition: color .3s;
}

.project-link:hover {
  color: var(--orange);
}

@media (width <= 1024px) {
  .about-content-wrapper {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .about-photo {
    order: -1;
    justify-content: center;
    display: flex;
  }

  .profile-photo-frame {
    max-width: 300px;
  }
}

@media (width <= 768px) {
  .hero-title {
    font-size: 42px;
  }

  .hero-description, .about-text p {
    font-size: 16px;
  }

  .about-highlights {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .highlight-number {
    font-size: 36px;
  }

  .hero-cta {
    flex-direction: column;
  }

  .cta-button {
    text-align: center;
    width: 100%;
  }

  .profile-photo-frame {
    max-width: 250px;
  }
}

.mouse-dot {
  pointer-events: none;
  z-index: 1;
  opacity: .4;
  will-change: transform;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  transition: opacity .3s;
  position: absolute;
}

.hero-section:hover .mouse-dot {
  opacity: .6;
}

@media (width <= 768px) {
  .mouse-dot {
    display: none;
  }
}
